<template>
    <div>
        <div class="link_detail_name link_city ">
          <li>
            <div class="right r">
              <div class="city" @click="toAddress">{{city}}</div>
              <i class="arrow-r"> </i>
            </div>
          </li>
          <v-distpicker type="mobile" @selected='selected'
                          v-show="addInp">
          </v-distpicker>
          <div class="mask" v-show="mask" @click="closeCity"></div>
        </div>
    </div>
</template>


<script>
 import VDistpicker from 'v-distpicker'
export default {
    name:"city",
    components: { VDistpicker },
    data(){
        return{
          city:'',
          addInp :false,
          mask:false,
        }
    },
     methods:{
       // sendOption2(){
       //   this.$emit("city",this.city)
       // },
      toAddress(){
        this.mask = true;
        this.addInp = true;
        },
      // 
      selected(data){
        this.mask =false;
        this.addInp = false;
        this.city = data.province.value + ' ' + data.city.value +' ' + data.area.value;
        this.$emit("city",this.city)
      },
      closeCity(){
        this.mask = false;
        this.addInp = false;
      },
     }
}
</script>

<style>
 @import "../../assets/css/city.css";
</style>




